//woocommerce and default pagination

ul.page-numbers,
.pagination {
	@include reset-list;
	padding: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	gap: var(--spacer-1);
	font-size: var(--fs-lg);
	li {
		margin: 0;
	}

	.page-numbers {
		--size: 3rem;
		width: var(--size);
		height: var(--size);
		display: flex;
		align-items: center;
		justify-content: center;
		border: var(--border-width) solid var(--border-color);
		border-radius: var(--border-radius);
		text-decoration: none;
		color: inherit;
		transition: border-color var(--transition);
		&.current {
			color: var(--color-white);
			background-color: var(--color-primary);
			border-color: var(--color-primary);
		}
		&:hover {
			border-color: var(--color-primary);
		}
	}
}
